@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhai+2:wght@600&family=Fraunces:opsz,wght@9..144,700&family=Fredoka+One&family=Merriweather&family=Montserrat:wght@100;400;500;700&family=Nunito&family=Rubik+Spray+Paint&family=Sacramento&family=Ubuntu:wght@300&display=swap);
.header{
    width:100%;
    display:flex;
    align-items: center;
    margin-bottom: 12px;
    margin-top: 0px;
}

.header-logo{
    height:27px;
    max-width: 140px;
    margin-right: 24px;
}

.header-right{
    display: flex;
    flex: 1 1;
    justify-content: space-between;
    align-items: center;
    height: 72px;


}

.header-location-search-container{
    height:54px;
    display: flex;
    align-items: center;
    border-radius: 8px;
    box-shadow: var(--primary-box-shadow);
    border: 1px solid var(--border-white);
    width: 70%;
}

.location-wrapper{
    display: flex;
    flex:1 1;
    justify-content: space-between;
    padding: 0px 10px;
}

.location-icon-name{
    display: flex;
    color: var(--zomato-gray);
    justify-content: flex-start;
    
}

.location-icon{
    color:var(--zomato-red);
    font-size: 20px;
    margin-right: 8px;
    width:16.4;
    height:20px;
    color: #cab1e8;;
}

.location-search-separartor{
    height:20px;
    border:0.5px solid var(--gray);
}

.header-searchbar{
    margin-right:8px;
    flex:2 1;
}

.search-icon{
   height :19px;
    color:var(--zomato-gray);
    margin:0px 12px;
    position: relative;
    top:4px;

}
.city-name{
    margin-bottom: 0;
}

.caret-icon{
    width:16px;
    height:16px;
    color: var(--zomato-gray);
}
.search-input{
    border: none;
    outline:none;
    width:60%;
    font-size: 14px;
}

.profile-wrapper{
    display: flex;
    justify-content: space-between;
    width: 15%;
    align-items: center;
}

.header-profile-image{
    height:50px;
    width:50px;
   
}
.tab-options{
 box-shadow: rgb(232, 232, 232) 0px -0.5px 0px inset;
}

.options-wrapper{
    display: flex;
}

.tab-item{
    margin-right: 24px;
    padding: 12px 12px 20px 12px;
}

.tab-name{
    font-size: 20px;
    font-weight: 600;
    margin-left: 10px;
    color:var(--inactive-text);
}

.tab-image-container{
    height: 60px;
    width: 60px;
    border-radius: 50%;
    background-color: var( --inactive-color);
}

.tab-image{
    height: 30px;
    width:30px;
}

.active-tab{
    border-bottom: 2px solid var(--zomato-red);
}

.active-tab .tab-name{
    color: var(--zomato-red);
}
.filters{
    padding: 16px 0px;
    display: flex;
    flex-wrap: wrap;
}

.filter-item{
    display: flex;
    background-color:var(--primary-bg) ;
    padding: 8px;
    color:var(--filter-text);
  border: 1px solid var(--gray);
  border-radius: 8px;
  margin:10px 10px 10px 0;
  font-size: 14px;
}

.filter-icon{
    color: var(--gray);
}
.filter-icon{
    width: 18px;
    height: 18px;
    -webkit-filter: brightness(0.5);
            filter: brightness(0.5);
    margin-right:5px;
    
}




.max-width{
    max-width:1110px;
    margin:0 auto;
}

.absolute-center{
    display: flex;
    align-items: center;
    justify-content: center;
    
}

.cur-po{
cursor: pointer;
}

.collection-title{
    font-size: 30px;
    color:var(--heading-text);
    font-weight: 600;
    margin-bottom: 32px;
}
#root{
    --primary-box-shadow:rgb(28 28 28 / 8%) 0px 2px 8px;
    --border-white:rgb(232,232,232);
    --zomato-gray:#828282;
    --zomato-red:#ff7e8b;
    --gray:rgb(207,207,207);
    --inactive-text:#696969;
    --inactive-color:#F8F8F8;
    --filter-text:#9c9c9c;
    --heading-text:#1C1C1C;
} 
.body{
  font-family: 'Nunito', sans-serif;
  margin:0px;
  box-sizing: border-box;
}



